import { useState } from "react";
import { Form, Button, Row, Col } from "react-bootstrap";
import styles from "../../styles/ProductEditPage.module.css"; // Make sure to import relevant CSS styles

const ProductEditPage = () => {
  const [product, setProduct] = useState({
    name: "",
    sku: "",
    price: "",
    category: "",
    status: "Active",
    mainImage: "",
    additionalImages: [],
    description: "",
  });

  const [isEditing, setIsEditing] = useState(false);

  const categories = ["Necklaces", "Bracelets", "Earrings", "Rings", "Watches"];

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const fileType = file.type.split("/")[0];
      if (fileType === "image") {
        setProduct({ ...product, mainImage: URL.createObjectURL(file) });
      }
    }
  };

  const handleAdditionalImageChange = (e) => {
    const files = Array.from(e.target.files);
    setProduct({
      ...product,
      additionalImages: files.map((file) => URL.createObjectURL(file)),
    });
  };

  const handleFormSubmit = (e) => {
    e.preventDefault();
    console.log(isEditing ? "Updating product:" : "Adding new product:", product);
  };

  return (
    <div className="container mt-5">
      <h2 className="text-center mb-4">{isEditing ? "Edit Product" : "Add Product"}</h2>

      {/* Product Edit Section - Single Box Shadow Card */}
      <div className={`${styles.cardContainer} shadow-sm mb-4`}>
        <div className="p-4">
          {/* Grid with two columns */}
          <Row>
            <Col md={6}>
              {/* Basic Details */}
              <div className={`${styles.innerCard} p-4`}>
                <h3>Basic Details</h3>
                <Form onSubmit={handleFormSubmit}>
                  <Form.Group controlId="name" className="mb-3">
                    <Form.Label>Product Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter product name"
                      value={product.name}
                      onChange={(e) =>
                        setProduct({ ...product, name: e.target.value })
                      }
                    />
                  </Form.Group>

                  <Form.Group controlId="sku" className="mb-3">
                    <Form.Label>SKU</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter SKU"
                      value={product.sku}
                      onChange={(e) =>
                        setProduct({ ...product, sku: e.target.value })
                      }
                    />
                  </Form.Group>

                  <Form.Group controlId="price" className="mb-3">
                    <Form.Label>Price</Form.Label>
                    <Form.Control
                      type="number"
                      placeholder="Enter price"
                      value={product.price}
                      onChange={(e) =>
                        setProduct({ ...product, price: e.target.value })
                      }
                    />
                  </Form.Group>

                  <Form.Group controlId="category" className="mb-3">
                    <Form.Label>Category</Form.Label>
                    <Form.Control
                      as="select"
                      value={product.category}
                      onChange={(e) =>
                        setProduct({ ...product, category: e.target.value })
                      }
                    >
                      {categories.map((category) => (
                        <option key={category} value={category}>
                          {category}
                        </option>
                      ))}
                    </Form.Control>
                  </Form.Group>

                  <Form.Group controlId="description" className="mb-3">
                    <Form.Label>Description</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Enter product description"
                      value={product.description}
                      onChange={(e) =>
                        setProduct({ ...product, description: e.target.value })
                      }
                    />
                  </Form.Group>

                  <Button variant="primary" type="submit">
                    {isEditing ? "Update Product" : "Add Product"}
                  </Button>
                </Form>
              </div>
            </Col>

            {/* Image Upload Section */}
            <Col md={6}>
              <div className={`${styles.innerCard} p-4`}>
                <h3>Product Image</h3>

                {/* Main Image Upload Box */}
                <Form.Group controlId="mainImage" className="mb-3">
                  <Form.Label>Upload Main Image</Form.Label>
                  {!product.mainImage ? (
                    <div
                      className="text-center"
                      onClick={() => document.getElementById("mainImageInput").click()}
                    >
                      <div className="plusIcon">+</div>
                      <input
                        id="mainImageInput"
                        type="file"
                        accept="image/*"
                        onChange={handleImageChange}
                        style={{ display: "none" }}
                      />
                    </div>
                  ) : (
                    <div className="text-center">
                      <img
                        src={product.mainImage}
                        alt="Main Product"
                        style={{
                          width: "150px",
                          height: "auto",
                          borderRadius: "8px",
                        }}
                      />
                    </div>
                  )}
                </Form.Group>

                {/* Additional Images Upload Box */}
                <Form.Group controlId="additionalImages" className="mb-3">
                  <Form.Label>Upload Additional Images</Form.Label>
                  <div className="text-center">
                    <div className="plusIcon">+</div>
                    <input
                      type="file"
                      accept="image/*"
                      multiple
                      onChange={handleAdditionalImageChange}
                      style={{ display: "none" }}
                    />
                  </div>
                  <div className="d-flex mt-3">
                    {product.additionalImages.map((image, index) => (
                      <div key={index} className="mr-2">
                        <img
                          src={image}
                          alt={`Additional Image ${index + 1}`}
                          style={{ width: "100px", height: "auto", borderRadius: "8px" }}
                        />
                      </div>
                    ))}
                  </div>
                </Form.Group>
              </div>
            </Col>
          </Row>
        </div>
      </div>
    </div>
  );
};

export default ProductEditPage;

